.contacts {
  background: linear-gradient(to top, #fff5f6 80%,var(--white-text-color) 80%);
}
@media screen and (min-width: 768px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.contacts-card {
  font-size: 12px;
  line-height: 1.83;
  letter-spacing: 0.04em;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;

    font-weight: 500;
    font-size: 14px;
    line-height: 1.64;
    margin: -16px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.81;
  }
}

.contacts-card__item {
  max-width: 100%;
  padding: 40px 32px;
  background-color: rgb(255, 255, 255);

  border-radius: 24px;

  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);

  &:hover,
  :focus {
    box-shadow: 0px 1px 3px rgba(173, 30, 30, 0.12),
      0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  }

  &:not(:last-child) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    margin: 16px;
    width: calc((100% - 96px) / 3);
  }
}

.contacts-card__list:not(:last-child) {
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 24px;
  margin-bottom: 24px;
}

.contacts-card__item-title {
  display: inline-flex;
  justify-content: center;
  width: 80px;

  margin-bottom: 30px;

  text-transform: uppercase;

  color:var(--white-text-color);

  background-color: #f0d1a5;
  border: 1px solid transparent;
  border-radius: 4px;

  @media screen and(min-width:768px) {
    width: 73px;

    font-weight: 500;
    font-size: 16px;
    line-height: 1.86;
    letter-spacing: 0.04em;

    color:var(--white-text-color);

    background-color: #f0d1a5;
    border: 1px solid transparent;
    border-radius: 6px;
  }

  @media screen and(min-width:1200px) {
    width: 75px;
  }
}

.contacts-card__item-title--green {
  background-color: #c2e297;
  width: 130px;

  @media screen and (min-width: 768px) {
    width: 138px;
  }
}

.contacts-card__item-city {
  margin-bottom: 8px;
}

.contacts-card__text {
  color: var(--grey-text-color);
}
.contacts-card__time {
  color: rgba(255, 165, 186, 1);
}

.contacts-card__item-working {
  margin-bottom: 8px;
}

.contacts-card__item-auth {
  border-bottom: none;
}

.contacts-card__link {
  color: var(--grey-text-color);

  &:hover,
  focus {
    color: #d41443;
  }
}

.contacts-card__btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
  justify-content: center;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    align-items: center;
    justify-items: center;

    min-width: 138px;

    margin: 0%;
  }
}

.btn {
  width: 162px;
}
.contacts-btn__item {
  @media screen and (min-width: 768px) {
    margin-right: 15px;
    margin-top: 60px;
  }
  &:not(:last-child) {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) {
      margin-bottom: 0px;
    }
  }
}

.btn::after {
  content: ' ';

  width: 8px;
  height: 10px;

  background-image: url(../images/arrow-white.svg);
  background-repeat: no-repeat;
  margin-left: 12px;

  background-size: contain;
  background-position: center;
}
.btn--red {
  &:hover,
  :focus {
    box-shadow: inset 3px 3px 10pxvar(--white-text-color);
  }
}

.btn--white {
  &:hover,
  :focus {
    box-shadow: inset 3px 3px 10px #da4141;
  }
}
.btn.btn--white::after {
  content: ' ';

  width: 8px;
  height: 10px;

  background-image: url(../images/arrow-red.svg);
  background-repeat: no-repeat;
  margin-left: 12px;

  background-size: contain;
  background-position: center;
}

